import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import styles from "./hero.module.css";
import hero from "../../img/hero.svg";
import light from "../../img/light-bulb-hero.svg";

const Hero = () => {
    const { t } = useTranslation();
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [formData, setFormData] = useState({ name: "", email: "", language: "", level: "" });

    const toggleModal = () => setIsModalOpen(!isModalOpen);

    const handleChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

    //       
    useEffect(() => {
        if (isModalOpen) {
            document.body.style.overflow = "hidden"; //    
        } else {
            document.body.style.overflow = "auto"; //   
        }
    }, [isModalOpen]);

    return (
        <div id="home" className={styles.heroSection}>
            <div className={styles.heroContent}>
                <div className={styles.afterHeroTittle}>
                    <h1 className={styles.heroTitle}>{t("heroTitle")}</h1>
                    <img className={styles.heroAfterEl} src={light} alt="Light" />
                </div>
                <span className={styles.heroDescription}>{t("heroDescription")}</span>
                <div className={styles.btn}>
                    <button className={styles.getStartedBtn} onClick={toggleModal}>{t("getStarted")}</button>
                </div>
            </div>
            <div className={styles.heroImage}>
                <img src={hero} alt="Hero" />
            </div>

            {isModalOpen && (
                <div className={styles.modalOverlay} onClick={toggleModal}>
                    <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
                        <div>  
                            <img className={styles.modalImg} src={hero} alt="hero" /> 
                        </div>
                        <div>
                            <h2 className={styles.formTitle}>{t("modalTitle")}</h2>
                            <p className={styles.formDesc}>{t("modalDescription")}</p>
                            <form>
                                <input 
                                    type="text" 
                                    name="name" 
                                    placeholder={t("namePlaceholder")} 
                                    value={formData.name} 
                                    onChange={handleChange} 
                                    required 
                                />
                                <input 
                                    type="email" 
                                    name="email" 
                                    placeholder={t("emailPlaceholder")} 
                                    value={formData.email} 
                                    onChange={handleChange} 
                                    required 
                                />
                                <input 
                                    type="tel" 
                                    name="phone" 
                                    placeholder={t("phonePlaceholder")} 
                                    value={formData.phone} 
                                    onChange={handleChange} 
                                    pattern="[\+]?[0-9]{1,4}?[0-9]{7,15}" 
                                    required 
                                />
                                <select 
                                    name="language" 
                                    value={formData.language} 
                                    onChange={handleChange} 
                                    required
                                >
                                    <option value="">{t("selectLanguage")}</option>
                                    <option value="english">English</option>
                                    <option value="spanish">Espaol</option>
                                    <option value="french">Franais</option>
                                </select>
                                <select 
                                    name="level" 
                                    value={formData.level} 
                                    onChange={handleChange} 
                                    required
                                >
                                    <option value="">{t("selectLevel")}</option>
                                    <option value="beginner">{t("beginner")}</option>
                                    <option value="intermediate">{t("intermediate")}</option>
                                    <option value="advanced">{t("advanced")}</option>
                                </select>
                                <button type="submit" className={styles.submitBtn}>{t("submit")}</button>
                            </form>
                            <button className={styles.closeBtn} onClick={toggleModal}>&times;</button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Hero;
