import React from "react";
import styles from './pricing.module.css';
import pr1 from '../../img/pricing1.svg';
import pr2 from '../../img/pricing2.svg';
import pr3 from '../../img/pricing3.svg';
import { useTranslation } from 'react-i18next';

const Pricing = () => {
    const { t } = useTranslation(); //   

    return (
        <div id="pricing" className={styles.priceSection}>
            <h2 className={styles.title}>{t('pricing')}</h2>
            <ul>
                <li>
                    <img className={styles.priceImg1} src={pr1} alt={t('selfStudyAlt')} />
                    <h4>{t('selfStudyTitle')}</h4>
                    <p className={styles.priceDesc}>{t('selfStudyDesc')}</p>
                    <p className={styles.priceCost}>
                        <span className={styles.value}> 1000</span>
                        <span className={styles.manth}>{t('perMonth')}</span>
                    </p>
                </li>
                <li>
                    <img className={styles.priceImg2} src={pr2} alt={t('liveClassesAlt')} />
                    <h4>{t('liveClassesTitle')}</h4>
                    <p className={styles.priceDesc}>{t('liveClassesDesc')}</p>
                    <p className={styles.priceCost}>
                        <span className={styles.value}> 200</span>
                        <span className={styles.manth}>{t('perMonth')}</span>
                    </p>
                </li>
                <li>
                    <img className={styles.priceImg3} src={pr3} alt={t('personalTuitionAlt')} />
                    <h4>{t('personalTuitionTitle')}</h4>
                    <p className={styles.priceDesc}>{t('personalTuitionDesc')}</p>
                    <p className={styles.priceCost}>
                        <span className={styles.value}> 300</span>
                        <span className={styles.manth}>{t('perMonth')}</span>
                    </p>
                </li>
            </ul>
        </div>
    );
};

export default Pricing;
