import React from "react"
import styles from './footer.module.css'
import telega from '../../img/telega.svg'
import inst from '../../img/inst.svg'
import mail from '../../img/mail.svg'

const Footer = () => {
    return (
        <div className={styles.footerContainer}>
            <div className={styles.footer}>
        <div className={styles.logo}>
            <h2>Nova<span>Moon</span></h2>
        </div>
        <div className={styles.nav}>
            <ul className={styles.navList}>
                <li className={styles.navItem}><img src={telega} alt="" /></li>
                <li className={styles.navItem}><img src={mail} alt="" /></li>
                <li className={styles.navItem}><img src={inst} alt="" /></li>
            </ul>
        </div>
    </div>
        </div>
        
    ) 
}

export default Footer