import { useState } from 'react';
import styles from "./skills.module.css";
import listening from '../../img/skills-listening.svg';
import reading from '../../img/skills-reading.svg';
import speaking from '../../img/skills-speaking.svg';
import writing from '../../img/skills-writing.svg';
import { useTranslation } from 'react-i18next';
import Modal from 'react-modal';

//        
Modal.setAppElement('#root');

const SkillCard = ({ image, title, description, extraText, buttonText, onClick }) => {
  return (
    <div className={styles.card}>
      <img className={styles.cardImage} src={image} alt={title} />
      <div className={styles.cardContent}>
        <h3>{title}</h3>
        <p className={styles.desc}>{description}</p>
        {extraText && <p>{extraText}</p>}
        <button className={styles.button} onClick={onClick}>{buttonText}</button>
      </div>
    </div>
  );
};

const Skills = () => {
  const { t } = useTranslation(); //   
  const [isModalOpen, setIsModalOpen] = useState(false); //   
  const [modalContent, setModalContent] = useState({}); //  

  const openModal = (title, detailedDescription) => {
    setModalContent({ title, detailedDescription });
    setIsModalOpen(true); //  
  };

  const closeModal = () => {
    setIsModalOpen(false); //  
  };

  //    
  const modalOverlayStyles = {
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 9999,
  };

  const modalContentStyles = {
    margin: "auto",
    backgroundColor: 'white',
    padding: '20px',
    maxWidth: '500px',
    width: '90%',
    height: "50%",
    borderRadius: '8px',
    textAlign: 'center',
    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
  };

  const modalCloseButtonStyles = {
    marginTop: '20px',
    padding: '10px 20px',
    backgroundColor: '#007bff',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
  };

  const modalCloseButtonHoverStyles = {
    backgroundColor: '#0056b3',
  };

  return (
    <section id="skills" className={styles.container}>
      <h2 className={styles.title}>{t('skills')}</h2>
      <div className={styles.skillsGrid}>
        {/* Speaking Card */}
        <SkillCard 
          image={speaking} 
          title={t('speaking')}
          description={t('speakingDescription')}
          extraText={t('speakingExtraText')}
          buttonText={t('learnMore')}
          onClick={() => openModal(
            t('speaking'),
            t('speakingDetailedDescription') //   
          )}
        />

        <div className={styles.threeCard}>
          <div className={styles.secondBlock}>
            {/* Writing Card */}
            <SkillCard 
              image={writing} 
              title={t('writing')}
              description={t('writingDescription')}
              buttonText={t('learnMore')}
              onClick={() => openModal(
                t('writing'),
                t('writingDetailedDescription') //    Writing
              )}
            />
            {/* Reading Card */}
            <SkillCard 
              image={reading} 
              title={t('reading')}
              description={t('readingDescription')}
              buttonText={t('learnMore')}
              onClick={() => openModal(
                t('reading'),
                t('readingDetailedDescription') //    Reading
              )}
            />
          </div>
          {/* Listening Card */}
          <div className={styles.cardLast}>
            <div className={styles.lastCardDesc}>
              <h3>{t('listening')}</h3>
              <p>{t('listeningDescription')}</p>
              <button
                className={styles.button}
                onClick={() => openModal(
                  t('listening'),
                  t('listeningDetailedDescription') //    Listening
                )}
              >
                {t('learnMore')}
              </button>
            </div>
            <img className={styles.cardImage} src={listening} alt="Listening" />
          </div>
        </div>
      </div>

      {/*     react-modal  - */}
      <Modal
        isOpen={isModalOpen}
        onRequestClose={closeModal}
        contentLabel="Skill Modal"
        style={{
          overlay: modalOverlayStyles,
          content: modalContentStyles,
        }}
      >
        <h3>{modalContent.title}</h3>
        <h4>Detailed Description</h4>
        <p>{modalContent.detailedDescription}</p>
        <button
          style={modalCloseButtonStyles}
          onMouseEnter={(e) => e.target.style.backgroundColor = modalCloseButtonHoverStyles.backgroundColor}
          onMouseLeave={(e) => e.target.style.backgroundColor = modalCloseButtonStyles.backgroundColor}
          onClick={closeModal}
        >
          Close
        </button>
      </Modal>
    </section>
  );
};

export default Skills;
